
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import { cloneObj } from '@/utils/utils.js'
import { setTimeout } from 'timers'
export default {
components: {
Scroller,


Search,
Group,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Spinner,
CellBox
},
activated() {
this.util.unmask()
},
created() {
this.util.init(this)
this.departId = this.sessionUtil.getDepartId()
this.userId = this.sessionUtil.getUserId()
this.refresh()
},
methods: {
searchFunc() {
this.refresh()
},
edit(Id) {
this.util.gotoByName('progressExcDetail', { Id: Id })
},
report(Id, xmid, AddName) {
this.util.gotoByName('prgReport', { Id, xmid, AddName })
},
loadMore() {
if (!this.fetching) {
this.page++
this.query('loadMore')
}
},
refresh() {
this.page = 1
this.query('refresh')
this.$nextTick(() => {
setTimeout(() => {
this.$refs.scroller.donePulldown()
this.pullupEnabled && this.$refs.scroller.enablePullup()
}, 10)
})
},
query(type) {
var _this = this,
url = '/admin/appAgentAction!runAgent.action',
params = {
agentId: 'Agent_WeChat_Project_Plan_Init',
type: 'projectPlanInit',
departId: this.departId,
start: (this.page - 1) * this.pageSize + 1,
end: (this.end = this.page * this.pageSize),
query: this.search
}
this.util.post(url, params, function(res) {
if (type === 'loadMore') {
if (res.data.length === _this.pageSize) {
_this.arr = _this.arr.concat(res.data)
_this.$nextTick(() => {
_this.$refs.scroller.reset()
})
_this.$refs.scroller.donePullup()
} else if (res.data.length < _this.pageSize) {
_this.arr = _this.arr.concat(res.data)
_this.$nextTick(() => {
_this.$refs.scroller.reset()
})
_this.isEnd = true
setTimeout(() => {
_this.changePullupStatus(false)
}, 10)
} else if (res.data.length === 0) {
_this.page--
_this.isEnd = true
setTimeout(() => {
_this.changePullupStatus(false)
}, 10)
}


} else if (type === 'refresh') {
if (res.data.length < _this.pageSize) {
_this.isEnd = true
_this.changePullupStatus(false)
} else {
_this.isEnd = false
_this.changePullupStatus(true)
}

_this.arr = res.data
_this.$refs.scroller.donePulldown()
}
})
},
changePullupStatus(enabled) {
this.$refs.scroller.donePullup()
if (enabled) {
this.$refs.scroller.enablePullup()
this.pullupEnabled = true
} else {
this.$refs.scroller.disablePullup()
this.pullupEnabled = false
}
},
handleEvents(type) {},
onButtonClick(type, Id) {
if (type === 'delete') {
this.util.deleteData(Id, 'U007_Project_Month_Report')
this.refresh()
}
},
addData(Id) {
this.util.gotoByName('progressExcDetail', { Id: Id })
}
},
data() {
return {
n: 10,
n1: 10,
pullupEnabled: true,
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
pulldown: {
content: '',
height: 60,
autoRefresh: false,
downContent: '',
upContent: '',
loadingContent: '...',
clsPrefix: 'xs-plugin-pulldown-'
},
pullup: {
content: '',
pullUpHeight: 50,
height: 40,
autoRefresh: false,
downContent: '',
upContent: '',
loadingContent: '...',
clsPrefix: 'xs-plugin-pullup-'
},
isEnd: false,
search: '',
arr: [],
arrOri: [],
departId: '',
userId: '',
page: 1,
pageSize: 10,
fetching: false
}
}
}



